.card {
    border: 4px solid;
    border-radius: 2px;
    width: 160px;
    height: 160px;
    margin: 1rem;
    position: relative;
    box-shadow: 2px 2px 5px rgb(56, 56, 56);
    transition: all .2s ease-in-out;
  }
  
  .card > .img-container {
    height: 100%;
    width: 100%;
    overflow: hidden;
    background:#ffffff;
  }
  
  .card > .img-container > img {
    height: 100%;
    width: 100%;
  }
  
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
.navbar {
  min-width: 100%;
}

.nav-link {
